<template>
	<div>
		<div class="top">
			<li :class="[activeIndex==0?'active':'']" @click="changeType(0)">答题技巧</li>
			<li :class="[activeIndex==1?'active':'']" @click="changeType(1)">注意事项</li>
			<li :class="[activeIndex==2?'active':'']" @click="changeType(2)">学习方法</li>
			<div class="top_line" :style="line"></div>
		</div>
		<div class="search_box">
			<div class="search_icon"></div>
			<input type="text" placeholder="搜索"/>
			
			<div class="btn">搜索</div>
			<div class="search_delete">
				<img src="../../../static/delete_search.png"/>
				
			</div>
		</div>
		<div v-for="item in list" class="item" @click="go(item.id)">
			<div class="left">
				<div class="avatar" >
				<img :src="item.imagePath" />
				</div>
			</div>
			<div class="right">
					<p style="margin-top: 0.3rem;">{{item.title}}</p>
					<div>{{item.abstract}}</div>
					<span class="time">{{item.timestamp}}</span>
			</div>
			
		</div>
		
	
	</div>
</template>

<script>
	
	export default{
		
		data(){
			return {
				activeIndex:0,
				list:[]
			}
		},
		computed:{
			line(){
				if(this.activeIndex==0){
					return {
						left :"12%"
					}
					
				}else if(this.activeIndex==1){
					return {
						left :"45%"
					}
					
					
				}else if(this.activeIndex==2){
					return {
						left :"78%"
					}
				}
			}
			
			
		},
		mounted(){
			
		this.getData(this.activeIndex+1)
			
		},
		methods:{
			changeType(num){
				this.activeIndex=num
				this.getData(this.activeIndex+1)
			},
			go(id){
					
				this.$router.push({name: 'NoticeItem',params:{NoticeId:id}})
			},
			getData(num){
				
		this.$Api.ArticleList({
			"s":this.$store.state.sessionId,
			"a":num
			
			
		}).then((res)=>{
			
		
			this.list=res.q.articles
			
		})
				
				
			}
		}
		
	}
	
</script>

<style scoped="scoped" lang="scss">
@import "../../sass/baseSass.scss";
.top{
	width: 100%;
	height: 0.88rem;
	border-bottom:0.02rem solid #CCCCCC ;
	position: relative;
	li{
		float:left;
		width: 33.33%;
		height: 100%;
		font-size: 0.3rem;
		text-align: center;
		line-height: 0.88rem;
	}
	.active{
		color:#fa8a12 ;
	}
	.top_line{
		position: absolute;
		bottom: 0.01rem;
		width: 0.8rem;
		height: 0.04rem;
		background: #fa8a12;
		transition: 0.5s;
		
	}
	
}
.search_box{
		height: 1rem;
		background: #f8f8f8;
		position: relative;
		input{
			float: left;
			width: 6rem;
			height: 0.6rem;
			background: #FFFFFF;
			border: none;
			border-radius:0.6rem ;
			margin: 0.2rem;
			text-indent: 0.8rem;
			font-size: 0.3rem;
		}
		.search_icon{
			width: 0.3rem;
			height: 0.3rem;
			background: url(../../../static/search.png) no-repeat;
			background-size:100% 100% ;
			position: absolute;
			left: 0.6rem;
			top: 0.34rem;
		}
		.search_delete{
			width: 0.3rem;
			height: 0.3rem;
			background: #999999;
			border-radius:50% ;
			position: absolute;
			top: 0.34rem ;
			right:20% ;
			img{
				width: 0.14rem;
				height: 0.14rem;
				float: left;
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				margin: auto;
			}
			
		}
		
		.btn{
		float: right;
		font-size: 0.3rem;
		text-align: center;
		line-height: 1rem;
		margin-right: 0.3rem;
	}
	}
.item{
	height: 1.45rem;
	border-bottom: 0.02rem solid #CCCCCC;
	.left{
		width: 20%;
		height: 100%;
		float: left;
		.avatar{
			width: 0.95rem;
			height: 0.95rem;
			border-radius:50% ;
		    position: relative;
			margin-left: 0.3rem;
			margin-top:0.25rem ;
			img{
				width: 100%;
				height: auto;
				position: absolute;
				top: 0;
				left: 0;
			}
		}
		
		
	}
	.right{
		width: 80%;
		height: 100%;
		float: left;
		font-size: 0.3rem;
		position: relative;
		div{
			
			color: #666666;
			display: -webkit-box;
            text-overflow: ellipsis;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 1;
            overflow: hidden;
		}
		.time{
			position: absolute;
			top: 0.35rem;
			right: 0.3rem;
			color: #999999;
			font-size: 0.24rem;
		}
	}
}	
	

</style>


